
import { defineComponent } from 'vue'

  export default defineComponent({
    name: 'flip',
    props: {
      activeClick: {
        type: Boolean,
        required: false,
        default: false
      },
      activeHover: {
        type: Boolean,
        required: false,
        default: false
      },
      width: {
        type: String,
        required: true
      },
      height: {
        type: String,
        required: true
      },
      transition: {
        type: String,
        required: false,
        default: '0.5s'
      },
      value: {
        type: Boolean,
        required: false
      }
    },
    data () {
      return {
        hover: false
      }
    },
    methods: {
      handleClick () {
        if (this.activeClick) {
          this.hover = !this.hover
          this.$emit('input', this.hover)
        }
      },
      handleMouseout () {
        if (this.activeHover) {
          this.$emit('input', false)
        }
      },
      handleMouseover () {
        if (this.activeHover) {
          this.$emit('input', true)
        }
      }
    },
    mounted () {
      if (this.value) {
        this.hover = this.value
      }
    },
    watch: {
      value (value) {
        this.hover = value
      }
    }
  })
